<template>
  <div class="moves-video">
    <div class="advance-video">
      <div class="hread">
         <van-nav-bar title="预告片" left-arrow :border="false" @click-left="back">
        <van-icon name="share" size="20px" slot="right"   color="white" />
        <van-icon name="arrow-left" size="19px" slot="left" color="white" />
      </van-nav-bar>
      </div>
      <div class="videa-mp4">
        <video autoplay  loop  controls>
          <source :src="this.vimg.videomp.resource_url" 
          type="video/mp4" />
        </video>
      </div>
    </div>
    <div class="video-text">
      <div class="video-title">
        {{this.vimg.videomp.title}}
      </div>
      <div class="video-filfle">
        <div class="fifle-fl">
          <div class="fifle-img">
            <img
              :src="this.vimg.images.small"
            />
          </div>
          <div class="fifle-fl-text">
            <div class="name-text1">{{this.vimg.title}}</div>
            <div class="name-text2">
              <span class="sp1">电影评分</span>
              <span class="sp2">{{this.vimg.rating.average}}.0</span>
              <span class="sp3">|</span>
              <span class="sp4">{{this.vimg.starname}}</span>
            </div>
          </div>
        </div>
        <div class="fifle-fr">购票</div>
      </div>
      <div class="video-synopsis">
        <h3>剧情简介</h3>
        <div class="synopsis-text">
          {{this.vimg.summary}}
        </div>
      </div>
    </div>
    <div class="video-leave">
      <van-search placeholder="说点什么吧" show-action left-icon="none" background="#F6F6F6">
        <div slot="action">发送</div>
      </van-search>
    </div>
  </div>
</template>
<style lang='less'>
.moves-video {
  background: white;
  .advance-video {
    width: 100%;
    overflow: hidden;
    background: black;
    position: sticky;
    left: 0;
    top: 0;
    .hread {
      width: 100%;
      // height: 24px;
      .van-nav-bar{
        background: black;
        color: white;
        .van-ellipsis{
          color: white;
        }
      }
    }
    .videa-mp4 {
      width: 100%;
      height: 229px;
      video{
           width: 100%;
      height: 229px;
     
      }
    }
  }
  .video-text {
    width: 345px;

    padding: 0 15px;
    overflow-y: auto;
    .video-title {
      width: 100%;
      font-size: 16px;
      padding: 15px 0;
      border-bottom: 1px solid #ededed;
    }
    .video-filfle {
      width: 100%;
      margin: 10px 0;
      height: 60px;
      background: #f5f5f5;
      overflow: hidden;
      border-radius: 3px;
      .fifle-fl {
        overflow: hidden;
        height: 100%;
        float: left;
        .fifle-img {
          width: 50px;
          height: 100%;
          float: left;
          border-radius: 5px;
          overflow: hidden;
          img {
            width: 50px;
            height: 100%;
          }
        }
        .fifle-fl-text {
          float: left;
          padding: 10px;
         
          .name-text1 {
            font-size: 14px;
             max-width: 180px;
          overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

          }
          .name-text2 {
            font-size: 14;
            margin-top: 5px;
             max-width: 180px;
          overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: #666666;
            .sp1 {
              color: #666666;
            }
            .sp2 {
              color: #ffa44b;
              font-weight: 600;
              margin-left: 5px;
            }
            .sp3 {
              margin: 0 3px;
              font-weight: 600;
            }
          }
        }
      }
      .fifle-fr {
        float: right;
        text-align: center;
        width: 50px;
        line-height: 30px;
        border-radius: 20px;
        border: 1px solid #ff4d63;
        color: #ff4d63;
        margin-top: 15px;
        margin-right: 15px;
      }
    }
    .synopsis-text{
      font-size: 12px;
      color: #A3A3A3;
      text-indent:2em;
    }
  }
  .video-leave {
    width: 100%;
    height: 50px;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #f6f6f6;
    .van-search {
      padding-right: 15px;
      background: #f6f6f6;
      .van-cell {
      }
      .van-cell__value {
        background: white;
        border: 1px solid #d7d7d5;
        border-radius: 8px;
      }
    }
  }
}
</style>
<script>
export default {
  name: "videot",
  data() {
    return {
        muted:true,
        vimg:''
    };
  },
  methods: {
       // 返回上一步
      back(){
       this.$router.go(-1);
      },
     
  },
  created() {
     
      //预告片的ID
     let viid = localStorage.getItem('videoID');
   
     //获取电影详情
     let vimg=JSON.parse(localStorage.getItem('detamove'));
    
     //合并演员
     vimg.starname = "";
     vimg.casts.forEach(v => {       
            vimg.starname += v.name + "、";
             vimg.starname = vimg.starname.slice(0, -1);       
            });
     vimg.trailers.forEach(v =>{
       if(v.id==viid){
         vimg.videomp=v;
       }
     })
     this.vimg=vimg;
      
  }
};
</script>